import {useEffect} from 'react';
import { Link, Route, Switch, useRouteMatch } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import {LoadUser} from '../../application/middlewares/auth';
import {getIsLogged} from '../../application/selectors/auth';
import Input from "@material-tailwind/react/Input";
import Button from "@material-tailwind/react/Button";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useForm, Controller} from "react-hook-form";
import { post } from '../../application/middlewares';
// import { GetCookie } from '../../utils/cookie';
import { loginSuccess, setToken } from '../../application/actions/auth';
import { GetCookie, SetCookie, RemoveCookie } from '../../utils/cookie'
import logo1 from '../image/logo1.svg'
import fon_gamysh from '../image/fon_gamysh.svg'
import {useContext} from 'react'
import {loginFormErrors} from './translations';
import Lang1 from '../Language/Lang1';
import { LangContext } from "../Language/LangContext";

function Login(){
    const { lang, dispatchLang } = useContext(LangContext);

    const {  control, handleSubmit, formState: { errors }, setError} = useForm({
        resolver: yupResolver(schema(lang))
    });

    const dispatch = useDispatch();
    const isLogged = useSelector(getIsLogged);
    const history = useHistory();
    console.log(lang,"------+++");
    // useEffect(() => {
    //     if(isLogged === false){
    //         dispatch(LoadUser({history}));
    //     }// eslint-disable-next-line
    // }, [dispatch, isLogged]);
//    const refresh_token = GetCookie('refresh_token')
    const onSubmit = (data) => {

        dispatch(post({
            url:'user/login',
            token:'',
            data,
            action:(response)=>{
                if(response.success){
                    // console.log("gelen login",response)
                    dispatch(setToken(response.data.token));
                    // console.log("token ", response.data.token)
                    const payload = {"token":response.data.token, "data":response.data.data}
                    // console.log(payload)
                    if (response.data.data && response.data.refresh_token){  
                        dispatch(loginSuccess(payload));
                        SetCookie('refresh_token', response.data.refresh_token);
                        history.push('/')
                    } else {
                        history.push('/auth/verifycode')
                    }
                    
                }else{
                    console.log("login Errors ",response);
                    // console.log(response?.message?.data?.error?.phone)
                    setError('phone', { type:'manual', message:`${lang === 'ru' ? "   " : "Telefon a-da aar sz aly"}`});
                    setError('password', { type:'manual', message:`${lang === 'ru' ? "   " : "Telefon a-da aar sz aly"}`});
                }
            },

        }))
        // dispatch(AdminRegister({data, setError, history}));
    }
    // console.log(setError)
    // console.log(errors.phone?.message, '-----')
    return(
        <div className="flex justify-center items-center h-full w-full fon_gamysh"
        style={{ backgroundImage: `url(https://gamysh.com/uploads/images/fon_gamysh.svg)` }}
        >
        <div className="flex max-w-sm mx-auto overflow-hidden  rounded-lg shadow-xl dark:bg-gray-800 lg:w-80">
        <form onSubmit={handleSubmit(onSubmit)} className="w-full px-6 py-8 md:px-8">
            <Link to='/' className='flex justify-center w-full'>
            <img src='https://gamysh.com/uploads/images/logo1.svg' className='auth_img' />
            </Link>
            <h2 className="text-2xl font-semibold text-center text-gray-700 dark:text-white my-5">{Lang1[lang].welcome}</h2>
            {/* <p className="text-xl text-center text-gray-600 dark:text-gray-200">Welcome back!</p> */}
            <div className="mt-4 w-full">
            <Controller
                    control={control}
                    name="phone"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="tel"  size="regular" color="blue"
                           outline={true} placeholder={Lang1[lang].phone} error={errors.phone?.message} />
                    )}}
                />
            </div>

            <div className="mt-9 w-full">
            <Controller
                    control={control}
                    name="password"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="password"  size="regular" color="blue"
                           outline={true} placeholder={Lang1[lang].password} error={errors.password?.message} />
                    )}}
                />
            </div>
            

            <Link to='/auth/forgot' className="flex justify-end text-sm text-gray-500 mt-5">
                {Lang1[lang].forgot}?
            </Link>

            <div className="mt-6 w-full flex justify-between items-start ">
            <div className="w-1/2 flex items-center justify-between">
                {/* <span className="border-b dark:border-gray-600 w-1/4"></span> */}

                <div className="text-xs w-4/6 text-gray-500 dark:text-gray-400 hover:underline text-center cursor-pointer">
                    <Link to='/auth/register'>
                    {Lang1[lang].dont_have}
                    </Link>
                    </div>
                {/* <span className="border-b dark:border-gray-600 w-1/4"></span> */}
            </div>
            <Button
                color="lightBlue"
                buttonType="link"
                size="regular"
                rounded={false}
                block={false}
                iconOnly={false}
                ripple="dark"
                // className='h-10'
            >
                {Lang1[lang].login}
            </Button>
            </div>
        </form>
    </div>
    </div>
    )
}
const schema = (lang) =>  Yup.object().shape({
    phone:Yup.string().min(8, Lang1[lang].phone_min).max(8, Lang1[lang].phone_max)
      .required(
        //   "Telefon nomer bolmaly"
        Lang1[lang].required
          )
      .matches(
        /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{4}\)?)?$/,
      Lang1[lang].only_number
    ),
    password: Yup.string().min(8, Lang1[lang].password_min).max(50, Lang1[lang].password_max).required(Lang1[lang].required).matches(
        /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
      Lang1[lang].password_regex
    ),
  });
export default Login;